import { Component, OnInit } from '@angular/core';
import {CordaContentService} from '../../Services/corda-content.service';

@Component({
  selector: 'app-single-transaction',
  templateUrl: './single-transaction.component.html',
  styleUrls: ['./single-transaction.component.css']
})
export class SingleTransactionComponent implements OnInit {
party;
singleTrade;
  constructor(public service: CordaContentService) { }

  ngOnInit() {
   this.singleTrade = Array.of(this.service.getSingleTrade());  
   if(location.port === '50005') {
    this.party = "O=Fidelity,L=London,C=GB";
 } else if(location.port === '50006'){
   this.party = "O=Market,L=New York,C=US";
 } else {
  this.party = 'O=Regulator,L=Delhi,C=IN';
 }
  }
  singletrade=()=>{
    return this.singleTrade;
  }
}
