import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { Http,Headers,RequestOptions,URLSearchParams } from '@angular/http';
import {Router} from '@angular/router';
// import { Http, Response, Headers, RequestOptions,URLSearchParams } from '@angular/http';
import { Subject, Observable } from 'rxjs';


@Injectable()
export class CordaContentService {
  payload;
  singleTrade;
  private trades = new Subject<any>();
  
  // Observable string streams
  tradeMethodCalled$ = this.trades.asObservable();
               
  constructor(private http:Http, private route : Router) { }

  getAllTrades()  {
 
    let headers = new Headers();
    console.log('here');
    console.log(location.host)
    headers.append('Content-Type', 'application/json');
    this.http.get('http://' + location.host + '/api/trading/trades',{
     headers: headers
   }).pipe(map((res : any)=>res.json()))
   .subscribe(
    (res) => {
     this.payload = res[0].state.data;
     console.log('success');
     console.log(this.payload);
     this.trades.next();
    }
   );
   }
  

  getSpecificTrade(linearID) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    let params: URLSearchParams = new URLSearchParams();
    params.set('linearID',linearID)
    let requestOptions=new RequestOptions;
    requestOptions.search = params;
    this.http.get('http://' + location.host + '/api/trading/getTrade',{
     headers: headers,search:params
   }).pipe(map((res:any)=>res.json()))
   .subscribe(
    (res) => {
     this.singleTrade=res[0];
     console.log(this.singleTrade);     
     this.route.navigate(['/api/trading/getTrade'], {queryParams : {linearID : linearID}})
     console.log('success');     
    }
   );
   }

   gettrades(){
     return this.payload;
  }
  getSingleTrade() {
    return this.singleTrade;
  }
  }