import { Component, OnInit } from '@angular/core';
import {CordaContentService} from '../../Services/corda-content.service';
import { ActivatedRoute, Router,NavigationEnd } from '@angular/router';


@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['./landing-page.component.css']
})
export class LandingPageComponent implements OnInit {
trades;
party;
  constructor(public service: CordaContentService,private routes : ActivatedRoute) { 
    this.service.tradeMethodCalled$.subscribe(
      () => {
        console.log(this.trades + "here");
        routes.params.subscribe(val => {
          this.trades=Array.of(this.service.gettrades());
          console.log(this.trades);
    })
    });
  }

  ngOnInit() {
    if(location.port === '50005') {
      this.party = "O=Fidelity,L=London,C=GB";
   } else if(location.port === '50006'){
     this.party = "O=Market,L=New York,C=US";
   } else {
    this.party = 'O=Regulator,L=Delhi,C=IN';
   }
  }

  getSpecificTransaction(linearID : String) {
    console.log(linearID)
     this.service.getSpecificTrade(linearID)
  }
  alltrades=()=>{
    return this.trades;
  }

}
